import React, {useState, useEffect} from 'react';
import useGetApiDetail from '../hooks/GetApiDetail'
import axios from 'axios';
import {csrftoken} from '../global/csrfToken';


export default function CategoryEdit({match}) {

  const [category_title, setCategory_title] = useState('');
  const [category_description, setCategory_description] = useState('');
  const [data, setData] = useState(null);

  const handleSubmit = () => {
    const data = {
      category_title: 'category_title',
      category_description: 'category_description'
    }
    axios.put('http://127.0.0.1:8000/api/v1/category/32/', data, {"X-CSRFToken": csrftoken }).then(res => {
      setData(res.data);
      setCategory_title('');
      setCategory_description('');

    })
  }

  return (
    <div className="container p-3">
      <div>
        <div classNames="form-group">
          <label htmlFor="category_title">category_title</label>
          <input
            type="text"
            className="form-control"
            id="category_title"
            placeholder="category_title"
            value={category_title}
            onChange={e => setCategory_title(e.target.value)} />
        </div>
        <div classNames="form-group">
          <label htmlFor="category_description" className="mt-2">category_description</label>
          <input
            type="text"
            className="form-control"
            id="category_description"
            placeholder="category_description"
            value={category_description}
            onChange={e => setCategory_description(e.target.value)} />
        </div>

        <button
          type="submit"
          className="btn btn-primary mt-3"
          onClick={handleSubmit}
        >--</button>
      </div>
    </div>
  );
}

