import React, { useState, useEffect } from 'react';
import { Table } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import useGetApi from '../hooks/GetApi';

export default function InventoryTable() {

    const name = 'inventory';
    let num = 1

    return(
        <div className="container">
        <h3 className="text-center">All Inventories</h3>
        <Table striped bordered hover variant="light" className="text-center">
          <thead>
            <tr>
              <th>№</th>
              <th>Serial</th>
              <th>Category</th>
              <th>Title</th>
              <th>Description</th>
              <th>Owner</th>
            </tr>
          </thead>
          <tbody>
            {useGetApi({name}).map(i => (
            <tr key={i.id}>
              <td className="col-1">{num++}</td>
              <td className="col-2">{i.serial_number}</td>
              <td className="col-2">{i.category}</td>
              <td className="text-start col-2"><Link to={{ pathname: `/inventory_detail/${i.id}`, fromDashboard: false }} className="ms-2">{i.title}</Link></td>
              <td className="col-3">{i.inventory_description}</td>
              <td className="col-2">{i.owner}</td>
            </tr>
            ))}
          </tbody>
        </Table>
            <div className="text-center">
                <Link to={{ pathname: '/inventory_add', fromDashboard: false }}><button className="btn btn-success">Add Inventory</button></Link>
            </div>
        </div>
    )
}
