import React, {useState, useEffect} from 'react';
import { Card } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import DeleteComp from '../delete/DeleteComp';
import EditComp from '../edit/EditComp';
import useGetApiDetail from '../hooks/GetApiDetail'


export default function InventoryDetail({match}) {

    const id = match.params.id
    const name = 'inventory'
    let inventory = useGetApiDetail({name, id})

    return(
        <div className="container">
        <h3 className="text-center">InventoryDetail</h3>
          <Card border="dark">
            <Card.Header>
            <div className="d-flex">
             <div className="me-5"><span className="text-muted">Serial Number: </span>{inventory.serial_number}</div>
                <div className="d-flex justify-content-around">
                    <EditComp id={id} name={'inventory'}/>
                    <DeleteComp id={id} name={'inventory'}/>
                </div>
              </div>
            </Card.Header>
            <Card.Body>
              <Card.Title>
                <p><span className="text-muted">Title: </span>{inventory.title}</p>
                <p><span className="text-muted">Category: </span><Link to={{ pathname: `/category_detail/${inventory.category}`, fromDashboard: false }}>{inventory.category}</Link></p>
                <p><span className="text-muted">Owner: </span><Link to={{ pathname: `/owner_detail/${inventory.owner}`, fromDashboard: false }}>{inventory.owner}</Link></p>
                <p><span className="text-muted">Price: </span>{inventory.price}$</p>
                <p><span className="text-muted">Date of purchase: </span>{inventory.date_purchase}</p>
              </Card.Title>
              <Card.Text>
                {inventory.inventory_description}
              </Card.Text>
            </Card.Body>
          </Card>
        </div>
    )
}
